.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background:#ffc600;
  font-family:'helvetica neue';
  font-size: 20px;
  font-weight: 200;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
input {
  width: 100%;
  padding:20px;
}

.search-form {
  max-width:400px;
  margin:50px auto;
}

input.search {
  margin: 0;
  text-align: center;
  outline:0;
  border: 10px solid #F7F7F7;
  width: 120%;
  left: -10%;
  position: relative;
  top: 10px;
  z-index: 2;
  border-radius: 5px;
  font-size: 40px;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.19);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.19);
}


.suggestions {
  margin: 0;
  padding: 0;
  position: relative;
  /*perspective:20px;*/
}
.suggestions li {
  background:white;
  list-style: none;
  border-bottom: 1px solid #D8D8D8;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.14);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.14);
  margin:0;
  padding:20px;
  -webkit-transition:background 0.2s;
  -o-transition:background 0.2s;
  transition:background 0.2s;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:justify;
      justify-content:space-between;
  text-transform: capitalize;
}

.suggestions li > *  {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
}
.suggestions li:nth-child(even) {
  -webkit-transform: perspective(100px) rotateX(3deg) translateY(2px) scale(1.001);
          transform: perspective(100px) rotateX(3deg) translateY(2px) scale(1.001);
  background: -webkit-gradient(linear,  left top, left bottom,  from(#ffffff),to(#EFEFEF));
  background: -webkit-linear-gradient(top,  #ffffff 0%,#EFEFEF 100%);
  background: -o-linear-gradient(top,  #ffffff 0%,#EFEFEF 100%);
  background: linear-gradient(to bottom,  #ffffff 0%,#EFEFEF 100%);
}
.suggestions li:nth-child(odd) {
  -webkit-transform: perspective(100px) rotateX(-3deg) translateY(3px);
          transform: perspective(100px) rotateX(-3deg) translateY(3px);
  background: -webkit-gradient(linear,  left bottom, left top,  from(#ffffff),to(#EFEFEF));
  background: -webkit-linear-gradient(bottom,  #ffffff 0%,#EFEFEF 100%);
  background: -o-linear-gradient(bottom,  #ffffff 0%,#EFEFEF 100%);
  background: linear-gradient(to top,  #ffffff 0%,#EFEFEF 100%);
}

span.population {
  font-size: 15px;
}

.hl {
  background:#ffc600;
}


